body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-tap-highlight-color: transparent;

  font-family: 'Asap', sans-serif;
  font-size: 20px;


  background: #141E30;  
  background: -webkit-linear-gradient(to left bottom, #243B55, #141E30); 
  background: linear-gradient(to left bottom, #243B55, #141E30);  
}
*{font-family: 'Asap', sans-serif; user-select: none;}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.text{
  color: white;

  margin-bottom: 60px;

  font-size: 2em;
  font-family: 'Exo 2', sans-serif;
}

input{
  width: 150px;
  height: 2em;

  font-size: 1em;
  text-align: center;

  margin-bottom: 1em;

  border-radius: 10px;
}

input::placeholder{
  font-family: 'Asap', sans-serif;
  font-size: 30px;
  line-height: 30px;
  text-align: center;
}

.app{
  position: relative;
  width: 100vw;
  height: 100vh;

  overflow: hidden;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.btn{
  cursor: pointer;
  margin-top: 40px;
  border-radius: 20px;

  font-family: 'Exo 2', sans-serif; 

  background-color: white;

  padding: 10px 30px;

  font-size: 1.5em;

  transition: all .2s;
}

.btn:hover{
  background-color: rgb(190, 186, 186);
}

.flag{
  position: absolute;
  top: 0;
  left: -10%;

  width: 120vw;

  border: 25px solid;

  border-top-color: #0057b8;
  border-bottom-color: #ffd700;
}

.flag.SS{
  top: unset;
  bottom: 0;

  border-top-color: #cc0000;
  border-bottom-color: #000000;
}

@media (max-width: 1200px){
  .btn:hover{
    background-color: white;
  }
}